import React from 'react'
import { AnimationOnScroll } from 'react-animation-on-scroll';

export default function Technologies() {

  return (
    <>
        <AnimationOnScroll 
            animatePreScroll="False" 
            animateIn="animate__fadeInRight" 
            animateOut='animate__fadeOutLeft' 
            duration="1"
        >
            <section id="about" class="content-section bg-light">
                <div class="container text-center">
                    <div class="row">
                        <div class="col-lg-10 mx-auto" style={{minHeight: "90vh"}}>

                            
                            <AnimationOnScroll
                                animatePreScroll="False" 
                                animateIn="animate__fadeInUp" 
                                duration="1.5"
                            >
                                <h2>Created with Bootstrap 5 and React-animate-on-scroll</h2>
                            </AnimationOnScroll>
                            

                            <AnimationOnScroll
                                animatePreScroll="False" 
                                animateIn="animate__fadeInUp" 
                                duration="1.4"
                            >
                                <p class="lead mb-5"><span>A beautify made react animations Package that's easy to implement and use! You can find it here at 
                                </span> <a href="https://github.com/MetinArslanturk/react-animation-on-scroll"> react-animation-on-scroll</a>
                                <span> github account by <strong>MetinArslanturk</strong>!</span></p>
                            </AnimationOnScroll>
                            
                            <AnimationOnScroll
                                animatePreScroll="False" 
                                animateIn="animate__fadeInUp" 
                                duration="1.3"
                            >
                                <div class="card">
                                    <div class="card-body" style={{filter: "hue-rotate(0deg)", padding: "56px"}}>
                                        <h4 class="card-title">Install React-Animate-On-Scroll using NPM</h4>
                                            <code class="text-bg-dark border rounded" style={{padding: "5px", color: "var(--bs-card-cap-bg)", filter: "blur(0px)"}}>
                                            <span style={{color: "rgb(201, 209, 217)", backgroundColor: "transparent"}}>
                                                npm install react-animation-on-scroll --save
                                            </span><br/>
                                            </code>
                                    </div>
                                </div>

                                <AnimationOnScroll
                                    animatePreScroll="False" 
                                    animateIn="animate__fadeInUp" 
                                    duration="1.2"
                                >
                                    <a class="btn btn-dark btn-xl js-scroll-trigger" role="button" href="#services" style={{marginTop: "72px"}}>Skills</a>
                                </AnimationOnScroll>

                            </AnimationOnScroll>
                        </div>
                    </div>
                </div>
            </section>
        </AnimationOnScroll>
    </>
  )
}
