import React from 'react'
import { AnimationOnScroll } from 'react-animation-on-scroll';


export default function SkillSets() {
  return (
    <>
        <AnimationOnScroll 
            animatePreScroll="False" 
            animateIn="animate__fadeInLeft" 
            animateOut='animate__fadeOutLeft' 
            duration="0.5"
        >
            <section id="services" class="content-section bg-primary text-white text-center" style={{minHeight:"100vh"}}>
                <div class="container">
                    <AnimationOnScroll 
                        animatePreScroll="False"
                        animateIn="animate__fadeInUp" 
                        animateOut='animate__fadeOutUp' 
                        duration="1"
                    >
                        <div class="content-section-heading">
                            <h3 class="text-secondary mb-0">Skill sets</h3>
                            <h2 class="mb-5">What Can I Offer</h2>
                        </div>
                        <p style={{marginBottom: "56px"}}>I possess several skills that make me a valuable asset in the field of Computer Science. Communication is a key strength of mine, and I am comfortable presenting technical ideas to a variety of audiences, both in writing and verbally. I have honed my programming skills in various languages, including Java, Python, and C++, and I am also proficient in data structures and algorithms. In addition, I have a solid grasp of mathematical concepts and can apply these principles to analyze and solve problems.<br/></p>
                    </AnimationOnScroll>
                    <div class="row">

                        <div class="col-md-6 col-lg-3 mb-5 mb-lg-0">
                            <AnimationOnScroll 
                                animatePreScroll="False"
                                animateIn="animate__fadeInUp" 
                                animateOut='animate__fadeOutUp' 
                                duration="1"
                            >
                                <span class="mx-auto service-icon rounded-circle mb-3"><i class="icon-screen-smartphone"></i></span>
                                <h4><strong>Responsive</strong></h4>
                                <p class="mb-0 text-faded">Design a responsive website using Bootstrap and ReactJS</p>
                            </AnimationOnScroll>
                        </div>
                        <div class="col-md-6 col-lg-3 mb-5 mb-lg-0">
                            <AnimationOnScroll 
                                animatePreScroll="False"
                                animateIn="animate__fadeInUp" 
                                animateOut='animate__fadeOutUp' 
                                duration="1"
                            >
                                <span class="mx-auto service-icon rounded-circle mb-3"><i class="icon-microphone"></i></span>
                                <h4><strong>Pitch and Host</strong></h4>
                                <p class="mb-0 text-faded">I can pitch idea and provides value using my communication skills</p>
                            </AnimationOnScroll>
                            
                        </div>
                        <div class="col-md-6 col-lg-3 mb-5 mb-lg-0">
                            <AnimationOnScroll 
                                animatePreScroll="False"
                                animateIn="animate__fadeInUp" 
                                animateOut='animate__fadeOutUp' 
                                duration="1"
                            >
                                <span class="mx-auto service-icon rounded-circle mb-3"><i class="icon-volume-2"></i></span>
                                <h4><strong>Communication</strong></h4>
                                <p class="mb-0 text-faded"><span>Communication is the key <br/> strength of mine</span><span></span></p>
                            </AnimationOnScroll>
                            
                        </div>
                        <div class="col-md-6 col-lg-3 mb-5 mb-lg-0">
                            <AnimationOnScroll 
                                animatePreScroll="False"
                                animateIn="animate__fadeInUp" 
                                animateOut='animate__fadeOutUp' 
                                duration="1"
                            >
                                <span class="mx-auto service-icon rounded-circle mb-3"><i class="icon-chart"></i></span>
                                <h4><strong>Market</strong></h4>
                                <p class="mb-0 text-faded">Provide a marketing guide to lead your business to success</p>
                            </AnimationOnScroll>
                            
                        </div>
                    
                    </div>
                </div>
            </section>
    </AnimationOnScroll>
    </>
  )
}
