import React from 'react'
import bg_masthead from '../img/bg-masthead.jpg';
import { motion } from "framer-motion"

import { AnimationOnScroll } from 'react-animation-on-scroll';

export default function Header() {
  return (
    <>
        <AnimationOnScroll 
            initiallyVisible="True"
            animatePreScroll="True" 
            animateIn="animate__fadeInUp" 
            duration="1"
        >
        <motion.div
            initial={{
                opacity: 0
            }}
            animate={{ 
                y: ['10px', '0px'],
                opacity: 10
            }}
            transition={{
                type: 'tween',
                duration: 1
            }}
        >
        <header id='page-top' class="d-flex masthead" style={{backgroundImage: `url(${bg_masthead})`}}>
            <div class="container my-auto text-center" style={{backdropFilter: `blur(0px)`}}>
                            
                <motion.div
                    initial={{
                        opacity: 0
                    }}
                    animate={{ 
                        y: ['100px', '0px'],
                        opacity: 1  
                    }}
                    transition={{
                        type: 'tween',
                        duration: 1,
                        delay: 0.1
                    }}

                >
                <h1 class="mb-1">LEO ALEGRE</h1>
                </motion.div>
                
                <motion.div
                    class="mb-5" 

                    initial={{
                        opacity: 0
                    }}
                    animate={{ 
                        y: ['100px', '0px'],
                        opacity: 1
                    }}
                    transition={{
                        type: 'tween',
                        duration: 1,
                        delay: 0.2
                    }}

                > 
                    <em>Personal Portfolio</em>
                </motion.div>
                
                <motion.div
                    initial={{
                        opacity: 0
                    }}
                    animate={{ 
                        y: ['100px', '0px'],
                        opacity: 100  
                    }}
                    transition={{
                        type: 'tween',
                        duration: 1,
                        delay: 0.5
                    }}

                >
                    <figure>
                        <blockquote class="blockquote">
                            <p class="mb-0" style={{paddingRight: "20%", paddingLeft: "20%", backdropFilter: "blur(0px)"}}>Hello, my name is Leo Daniel Alegre, and I am pleased to present my portfolio. I am a highly motivated individual who has developed a diverse set of skills throughout my education and professional experiences. I have studied Computer Science at Caraga State University, where I gained a solid foundation in my chosen field and developed a passion for continuous learning.<br/></p>
                        </blockquote>
                        <figcaption class="blockquote-footer"></figcaption>
                    </figure>
                </motion.div>
                
                <motion.div
                    initial={{
                        opacity: 0
                    }}
                    animate={{ 
                        y: ['10px', '0px'],
                        opacity: 100  
                    }}
                    transition={{
                        type: "spring", 
                        stiffness: 100,
                        delay: 2.5,
                        x: { duration: 1 },
                        default: { ease: "linear" }
                    }}
                >
                    <a class="btn btn-primary btn-xl js-scroll-trigger" role="button" href="#about">Explore</a>
                </motion.div>
                
                
                <div class="overlay"></div>
                
            </div>
            
        </header>
    </motion.div>
    </AnimationOnScroll>
    </>
  )
}
