import React from 'react'

import { AnimationOnScroll } from 'react-animation-on-scroll';

import img1 from '../img/portfolio-1.jpg';
import img2 from '../img/portfolio-2.jpg';
import img3 from '../img/portfolio-3.jpg';
import img4 from '../img/portfolio-4.jpg';


export default function Portfolio() {
  return (
    <>
    <AnimationOnScroll 
        initiallyVisible="True"
        animatePreScroll="False" 
        animateIn="animate__fadeInLeft" 
        animateOut="animate__fadeOutLeft" 
        duration="1"
    >
        <section id="portfolio" class="content-section">
            <div class="container">

                <div class="content-section-heading text-center">
                    <h3 class="text-secondary mb-0">Portfolio</h3>
                    <h2 class="mb-5">Recent Projects</h2>
                </div>

                <div class="row g-0">
                    <div class="col-lg-6">
                        <AnimationOnScroll 
                            initiallyVisible="True"
                            animatePreScroll="False" 
                            animateIn="animate__fadeInLeft" 
                            animateOut="animate__fadeOutLeft" 
                            duration="1"
                        >
                            <a class="portfolio-item" href="#">
                                <div class="caption">
                                    <div class="caption-content">
                                        <h2>Project 1</h2>
                                        <p class="mb-0">A yellow pencil with envelopes on a clean, blue backdrop!</p>
                                    </div>
                                </div>
                                <img class="img-fluid" src={img1}/>
                            </a>
                        </AnimationOnScroll>
                    </div>

                    <div class="col-lg-6">
                        <AnimationOnScroll 
                            initiallyVisible="True"
                            animatePreScroll="False" 
                            animateIn="animate__fadeInDown" 
                            animateOut="animate__fadeOutDown" 
                            duration="1.5"
                        >
                            <a class="portfolio-item" href="#">
                                <div class="caption">
                                    <div class="caption-content">
                                        <h2>project 2</h2>
                                        <p class="mb-0">A dark blue background with a colored pencil, a clip, and a tiny ice cream cone!</p>
                                    </div>
                                </div>
                                <img class="img-fluid" src={img2}/>
                            </a>
                        </AnimationOnScroll>
                    </div>

                    <div class="col-lg-6">
                        <AnimationOnScroll 
                                initiallyVisible="True"
                                animatePreScroll="False" 
                                animateIn="animate__fadeInUp" 
                                animateOut="animate__fadeOutUp" 
                                duration="2"
                        >
                            <a class="portfolio-item" href="#">
                                <div class="caption">
                                    <div class="caption-content">
                                        <h2>project 3</h2>
                                        <p class="mb-0">Strawberries are such a tasty snack, especially with a little sugar on top!</p>
                                    </div>
                                </div>
                                <img class="img-fluid" src={img3}/>
                            </a>
                        </AnimationOnScroll>
                    </div>

                    <div class="col-lg-6">
                        <AnimationOnScroll 
                                initiallyVisible="True"
                                animatePreScroll="False" 
                                animateIn="animate__fadeInRight" 
                                animateOut="animate__fadeOutRight" 
                                duration="2.5"
                            >
                            <a class="portfolio-item" href="#">
                                <div class="caption">
                                    <div class="caption-content">
                                        <h2>project 4</h2>
                                        <p class="mb-0">A yellow workspace with some scissors, pencils, and other objects.</p>
                                    </div>
                                </div>
                                <img class="img-fluid" src={img4}/>
                            </a>
                        </AnimationOnScroll>
                    </div>

                </div>
            </div>
        </section>
    </AnimationOnScroll>
    </>
  )
}
