import React, { useState } from 'react'

export default function NavigationBar() {

    const [active, setActive] = useState('')
    const [icon, setIcon] = useState('fa fa-bars')

    const handleActive = () => {
        if(active == "active"){
            setActive("")
            setIcon("fa fa-bars")
        }else{
            setActive("active")
            setIcon("fa fa-times")
        }
    }

    return (
        <>
            <a className={`menu-toggle rounded ${active}`} onClick={handleActive}><i className={`${icon}`} /></a>
            <nav className={`navbar navbar-light navbar-expand ${active}`} id="sidebar-wrapper">
                <div className="container">
                    <button data-bs-toggle="collapse" className="navbar-toggler d-none" data-bs-target="#" />
                    <div className="collapse navbar-collapse">
                    <ul className="navbar-nav sidebar-nav" id="sidebar-nav">
                        <li className="nav-item sidebar-brand"><a className="nav-link active js-scroll-trigger" href="#page-top">LEO ALEGRE</a></li>
                        <li className="nav-item sidebar-nav-item"><a className="nav-link js-scroll-trigger" href="#page-top">Home</a></li>
                        <li className="nav-item sidebar-nav-item"><a className="nav-link js-scroll-trigger" href="#about">About</a></li>
                        <li className="nav-item sidebar-nav-item"><a className="nav-link js-scroll-trigger" href="#services">Services</a></li>
                        <li className="nav-item sidebar-nav-item"><a className="nav-link js-scroll-trigger" href="#education">Education</a></li>
                        <li className="nav-item sidebar-nav-item"><a className="nav-link js-scroll-trigger" href="#portfolio">Portfolio</a></li>
                        <li className="nav-item sidebar-nav-item"><a className="nav-link js-scroll-trigger" href="#contact">Contact</a></li>
                    </ul>
                    </div>
                </div>
            </nav>
        </>
    )
}
