import React from 'react'
import { motion } from 'framer-motion'

export default function Footer() {


  return (
    <>
        <footer class="footer text-center">
            <div class="container">
                <motion.ul class="list-inline mb-5">
                    <motion.li class="list-inline-item">&nbsp;<a class="link-light social-link rounded-circle" href="#"><i class="icon-social-facebook"></i></a></motion.li>
                    <motion.li class="list-inline-item">&nbsp;<a class="link-light social-link rounded-circle" href="#"><i class="icon-social-twitter"></i></a></motion.li>
                    <motion.li class="list-inline-item">&nbsp;<a class="link-light social-link rounded-circle" href="#"><i class="icon-social-github"></i></a></motion.li>
                </motion.ul>
                <p class="text-muted mb-0 small">Copyright &nbsp; Brand 2023</p>
            </div><a class="js-scroll-trigger scroll-to-top rounded" href="#page-top"><i class="fa fa-angle-up"></i></a>
        </footer>
    </>
  )
}
