import React from 'react'
import { AnimationOnScroll } from 'react-animation-on-scroll';

export default function Contact() {
  return (
    <>
        <section id="contact" class="content-section bg-primary text-white">
          <AnimationOnScroll 
              animatePreScroll="False" 
              animateIn="animate__fadeInDown" 
              animateOut='animate__fadeOutUp' 
              duration="0.5"
          >
            <p class="lead text-center" style={{marginBottom: "32px", marginRight: "5vw", marginLeft: "5vw"}}>In conclusion, I am a dedicated and passionate individual who has developed a diverse skill set through my education and professional experiences in the field of Computer Science. I am confident that my communication, programming, mathematical, and other skills make me a valuable addition to any team. Thank you for taking the time to review my portfolio, and I look forward to the opportunity to showcase my skills and contribute to your organization.</p>
            <div class="container text-center">
                <h2 class="mb-4">Do You Want To Collaborate?</h2>
                <a class="btn btn-light btn-xl me-4" role="button" href="#">Learn more</a>
                <a class="btn btn-dark btn-xl me-4" role="button" href="#">Contact Me</a>
            </div>
          </AnimationOnScroll>
        </section>
    </>
  )
}
