import React from 'react'
import bg_callout from '../img/bg-callout.jpg';

import { AnimationOnScroll } from 'react-animation-on-scroll';

export default function Education() {
  return (
    <>
    <AnimationOnScroll 
        initiallyVisible="True"
        animatePreScroll="False" 
        animateIn="animate__fadeInRight" 
        animateOut="animate__fadeOutRight" 
        duration="1"
    >
        <section id="education"  class="callout" style={{background:`linear-gradient(90deg, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.1) 100%), url(${bg_callout})`, backgroundPosition: "center center", backgroundRepeat: "no-repeat", backgroundSize: "cover"}}>
            <AnimationOnScroll 
                initiallyVisible="True"
                animatePreScroll="True" 
                animateIn="animate__fadeInUp" 
                duration="1"
            >
            <div class="container text-center">
                <h2 class="mx-auto mb-5"><em></em><span class="text-center">EDUCATION<br/></span></h2>
            </div>
            </AnimationOnScroll>
            <AnimationOnScroll 
                initiallyVisible="True"
                animatePreScroll="True" 
                animateIn="animate__fadeInUp" 
                duration="1"
            >
            <div class="card" style={{maxWidth: "50vw", margin: "auto", background: "#ffffff"}}>
                <div class="card-body" style={{padding: "24px"}}>
                    <h4 class="text-center card-title">STUDIED AT</h4>
                    <h6 class="text-center text-muted card-subtitle mb-2">Caraga State University - Main Campus</h6>
                    <p class="text-center card-text">I earned my degree in Computer Science from Caraga State University, where I studied a range of subjects including computer architecture, operating systems, database systems, software engineering, and artificial intelligence.&nbsp;</p>
                    <p class="text-center card-text">During my time there, I honed my critical thinking, analytical, and research skills, which have proved invaluable throughout my professional experiences. I also gained experience working collaboratively with peers, completing group projects and assignments, and developing strong interpersonal skills.</p>
                </div>
            </div>
            </AnimationOnScroll>
        </section>

    </AnimationOnScroll>
    </>
  )
}
